* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

nav ul, ul.clean {
  margin-left: 0;
  list-style-type: none !important;
}

a, a:visited {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

a:focus {
  outline: none;
}

a::-moz-focus-inner {
  border: 0;
}

input, select, textarea {
  outline: 0;
}

input[type="button"] {
  outline: none;
}

input[type="button"]::-moz-focus-inner {
  border: 0;
}

button {
  outline: none;
}

button::-moz-focus-inner {
  border: 0;
}

img {
  border: 0 !important;
  outline: 0 !important;
}

input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

html {
  text-align: center;
  font-size: 10px;
}

body {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
  width: 100%;
  height: 100%;
  line-height: 1.4em;
  position: relative;
}

header, footer, section, article, nav {
  display: block;
}

input, button, textarea {
  font-family: var(--ssr-font-family);
}

input {
  color: #fff;
}

input[type="number"] {
  margin: 0;
  -webkit-appearance: none !important;
}

input::-webkit-outer-spin-button {
  margin: 0;
  -webkit-appearance: none !important;
}

input::-webkit-inner-spin-button {
  margin: 0;
  -webkit-appearance: none !important;
}

.btn {
  color: #fff;
  background-color: var(--chrome);
  text-transform: uppercase;
  font-family: var(--ss-font-family);
  border: 1px solid var(--white-opacity-01);
  webkit-transition: all .2s ease-in-out 0s;
  border-radius: 3px;
  transition: all .2s ease-in-out;
}

.btn:hover {
  cursor: pointer;
  background-color: #777;
}

.flat-btn {
  color: #fff;
  background-color: #0000;
  border: none;
}

.request-btn {
  color: #fff;
  background-color: var(--chrome);
  font-family: var(--ss-font-family);
  border: 1px solid var(--white-opacity-01);
  webkit-transition: all .2s ease-in-out 0s;
  border-radius: 3px;
  padding: 5px 10px;
  transition: all .2s ease-in-out;
}

.file-btn {
  color: var(--gray);
  height: 30px;
  margin: 0 auto 20px;
  position: relative;
}

.file-btn-native {
  z-index: 0;
  border: 1px solid var(--dark);
  height: 30px;
  margin-left: 32px;
  line-height: 27px;
  position: relative;
}

.file-btn-label {
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #fff;
  background-color: var(--chrome);
  z-index: 1;
  border-radius: 3px;
  width: 120px;
  height: 30px;
  padding: 0 14px;
  font-family: sans-serif;
  font-size: 8px;
  line-height: 29px;
  display: inline-block;
  position: absolute;
}

.h1, .h2, .h3, h4, .h5, .t1, .t2, .t3, .t4, .t5, .t6, .a, .text, button, .btn, input, svg, a, .flat-btn, .settings-switch-bnt {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
}

.a {
  color: #bbb;
  text-transform: uppercase;
  cursor: pointer;
}

.line {
  vertical-align: middle;
  background-color: var(--white-opacity-01);
  width: 100%;
  height: 1px;
  margin: 0 auto;
  display: block;
}

.cf:before, .cf:after {
  content: " ";
  display: table;
}

.cf:after {
  clear: both;
}

.hide {
  visibility: hidden;
}

:root {
  --ssb-font-family: "Roboto", sans-serif;
  --ssr-font-family: "Roboto", sans-serif;
}

body {
  --black: #000;
  --white: #fff;
  --gray: #aaa;
  --green: #40ff70;
  --red: #ff495f;
  --yellow: #f8c73a;
  --dark: #28272d;
  --chrome: #35363a;
  --zone-red: #fe340b;
  --zone-orange: #ff663a;
  --zone-yellow: #f8c73a;
  --zone-green: #57c057;
  --zone-teal: #44a5ab;
  --zone-blue: #328aff;
  --zone-gray: #636468;
  --orange-logo: #fc4c02;
  --white-gray: #f7f7fa;
  --red-logo: #e50914;
  --progress-white: #fff4;
  --black-opacity-01: #0000001a;
  --black-opacity-02: #0003;
  --white-opacity-01: #ffffff1a;
  --white-opacity-03: #ffffff4d;
}

.zone-one {
