}

.zone-two {
  background-color: var(--zone-blue);
}

.zone-three {
  background-color: var(--zone-teal);
}

.zone-four {
  background-color: var(--zone-green);
}

.zone-five {
  background-color: var(--zone-yellow);
}

.zone-six {
  background-color: var(--zone-orange);
}

.zone-seven {
  background-color: var(--zone-red);
}

.zone-one-color {
  color: var(--zone-gray);
}

.zone-two-color {
  color: var(--zone-blue);
}

.zone-three-color {
  color: var(--zone-teal);
}

.zone-four-color {
  color: var(--zone-green);
}

.zone-five-color {
  color: var(--zone-yellow);
}

.zone-six-color {
  color: var(--zone-orange);
}

.zone-seven-color {
  color: var(--zone-red);
}

.black {
  color: var(--black);
}

.white {
  color: var(--white);
}

.gray {
  color: var(--gray);
}

.yellow {
  color: var(--yellow);
}

.red {
  color: var(--red);
}

.green {
  color: var(--green);
}

.black-bg {
  background-color: var(--black);
}

.white-bg {
  background-color: var(--white);
}

.gray-bg {
  background-color: var(--gray);
}

.yellow-bg {
  background-color: var(--yellow);
}

.red-bg {
  background-color: var(--red);
}

.green-bg {
  background-color: var(--green);
}

.gray-zone {
  background-color: var(--zone-gray);
}

.blue-zone {
  background-color: var(--zone-blue);
}

.green-zone {
  background-color: var(--zone-green);
}

.yellow-zone {
  background-color: var(--zone-yellow);
}

.orange-zone {
  background-color: var(--zone-orange);
}

.red-zone, .purple-zone {
  background-color: var(--zone-red);
}

.chrome-bg {
  background-color: var(--chrome);
}

.dark-bg {
  background-color: var(--dark);
}

body {
  color: var(--white);
  background-color: var(--dark);
}

.connections-header {
  background-color: var(--chrome);
}

#progress-active {
  background-color: #0000;
}

.radio {
  fill: var(--white);
}

.mode.active {
  color: #fff;
}

.icon-btn .icon {
  fill: var(--gray);
}

.icon-btn .text {
  color: var(--gray);
}

.tab-btn.active .text {
  color: var(--white);
}

.tab-btn.active .icon-fill {
  fill: var(--white);
}

.control-icon {
  fill: #fff;
}

.wrapper {
  font-family: var(--ssr-font-family);
  align-content: center;
  width: 100%;
  min-height: 100vh;
  position: relative;
  overflow-x: hidden;
}

.page {
  width: 100%;
  min-height: 110vh;
  padding-bottom: 12em;
  transition: all .2s ease-in-out;
  display: none;
}

.page.active {
  display: block;
}

.page--header {
  width: 100%;
}

.page--heading {
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
  font-size: 1.1em;
  line-height: 4em;
}

#workouts-page .page--header {
  margin-bottom: 40px;
}

#home-page {
  z-index: 1;
  min-height: 100vh;
  padding: 0;
}

#home-page-wrapper {
  grid-template-rows: 50px 2.5fr 1fr 1fr 98px 100px;
  grid-template-columns: 100%;
  height: 100vh;
  display: grid;
}

#compatibility--cont {
  background-color: var(--red);
  border-bottom: 1px solid var(--dark);
  z-index: 1;
  width: 100%;
  padding: 1em .5em;
  font-size: 1.4em;
  line-height: 1.5em;
}

#compatibility--cont a {
  color: var(--yellow);
}

.connections-header {
  flex-direction: row;
  grid-row: 1;
  place-content: center space-evenly;
  align-items: center;
  font-size: 1.1rem;
  display: flex;
}

.connection-switch, .protocol-switch {
  align-content: center;
  align-items: center;
  height: 3em;
  padding: 0 15px;
  display: flex;
}

.connection-switch:hover, .connection-icon-switch:hover {
  cursor: pointer;
}

.connection-switch--indicator {
  border-radius: 50%;
  width: 1.3em;
  height: 1.3em;
  margin: 0 1em 0 0;
}

.connection-switch--indicator.on, .connection-icon-switch--indicator.on {
  background-color: var(--green);
}

.connection-switch--indicator.loading, .connection-icon-switch--indicator.loading {
  background-color: var(--yellow);
}

.connection-switch--indicator.off, .connection-icon-switch--indicator.off {
  background-color: var(--red);
}

.connection-switch--label {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1em;
  line-height: 1em;
}

.protocol-switch.on {
  color: var(--white);
}

.protocol-switch.loading, .protocol-switch.off {
  color: var(--gray);
}

.protocol-switch.on .protocol-btn--icon {
  fill: var(--white);
}

.protocol-switch.loading .protocol-btn--icon, .protocol-switch.off .protocol-btn--icon {
  fill: var(--gray);
}

.protocol-btn {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  display: flex;
}

.protocol-btn--icon {
  fill: var(--white);
  width: var(--size--icon--global);
  height: var(--size--icon--global);
}

.protocol-btn--label {
  margin-left: 10px;
  font-weight: bold;
}

.data-tiles {
  --data-tile--heading-font-size: 1em;
  --data-tile--value-font-size: 3.2em;
  --data-tile--unit-font-size: 1em;
  --data-tile-small--heading-font-size: 1em;
  --data-tile-small--value-font-size: 2.6em;
  --data-tile-small--unit-font-size: .6em;
  --data-tile--heading-margin: 0 auto 1em;
  grid-row: 2;
  grid-template-rows: repeat(3, 1fr) 2rem;
  grid-template-columns: repeat(12, 1fr);
  place-items: center;
  font-size: 1rem;
  display: grid;
  position: relative;
}

#data-tile--power {
  grid-area: 1 / 1 / 1 / 5;
}

#data-tile--interval-time {
  grid-area: 1 / 5 / 1 / 9;
}

#data-tile--heart-rate {
  grid-area: 1 / 9 / 1 / 13;
}

#data-tile--target {
  cursor: pointer;
  grid-area: 2 / 1 / 2 / 5;
}

#data-tile--elapsed-time {
  grid-area: 2 / 5 / 2 / 9;
}

#data-tile--cadence {
  grid-area: 2 / 9 / 2 / 13;
}

#data-tile--speed {
  grid-area: 3 / 1 / 3 / 4;
  display: none;
}

#data-tile--distance {
  grid-area: 3 / 4 / 3 / 7;
  display: none;
}

#data-tile--slope {
  grid-area: 3 / 7 / 3 / 10;
  display: none;
}

#data-tile--power-avg {
  grid-area: 3 / 10 / 3 / 13;
  display: none;
}

#data-tile--smo2 {
  grid-area: 3 / 1 / 3 / 4;
  display: none;
}

#data-tile--thb {
  grid-area: 3 / 4 / 3 / 7;
  display: none;
}

#data-tile--core-body-temperature {
  grid-area: 3 / 7 / 3 / 10;
  display: none;
}

#data-tile--skin-temperature {
  grid-area: 3 / 10 / 3 / 13;
  display: none;
}

#data-tile--power-avg.active, #data-tile--slope.active, #data-tile--speed.active, #data-tile--distance.active, #data-tile--smo2.active, #data-tile--thb.active, #data-tile--core-body-temperature.active, #data-tile--skin-temperature.active {
  display: block;
}

.data-tile--switch--group {
  grid-area: 4 / 1 / 4 / 13;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 2rem;
  display: flex;
}

.data-tile--switch {
  justify-content: center;
  align-items: center;
  width: 6rem;
  height: 2rem;
  display: flex;
}

.data-tile--switch .data-tile--switch--icon {
  border: 1px solid #fff;
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  display: block;
}

.data-tile--switch.active .data-tile--switch--icon {
  background-color: #fff;
  border: 1px solid #fff;
}

.data-tile-small--unit {
  display: none;
}

.data-tile--heading {
  font-size: var(--data-tile--heading-font-size);
  margin: var(--data-tile--heading-margin);
  font-family: var(--ssr-font-family);
  text-transform: uppercase;
}

.data-tile--value {
  font-size: var(--data-tile--value-font-size);
  line-height: 1em;
  font-family: var(--ssb-font-family);
  font-weight: bold;
}

.data-tile-small--heading {
  margin: var(--data-tile--heading-margin);
  font-size: var(--data-tile-small--heading-font-size);
  text-transform: uppercase;
}

.data-tile-small--heading sub {
  text-transform: lowercase;
}

.data-tile-small--value {
  font-size: var(--data-tile-small--value-font-size);
  font-weight: bold;
  line-height: 1em;
}

.data-tile-small--unit {
  font-size: var(--data-tile-small--unit-font-size);
  font-weight: bold;
}

.data-tile--value-cont.complex {
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  height: 3.2em;
  display: flex;
}

.data-tile--value.active {
  width: 2em;
  font-size: 2.6em;
}

.data-tile-target--value {
  width: 0;
  display: none;
}

.data-tile-target--value.active {
  width: auto;
  min-width: 1.5em;
  font-size: 1.6em;
  display: block;
}

z-stack-item {
  cursor: pointer;
  display: none;
}

z-stack-item.active {
  display: block;
}

.graph {
  --graph-total-height: 100%;
  height: var(--graph-total-height);
  border-top: 1px solid var(--white-opacity-01);
  grid-template-rows: minmax(1.4rem, auto) minmax(0, 100%);
  width: 100%;
  font-size: 1rem;
  display: grid;
  position: relative;
}

#graph-power {
  overflow: hidden;
}

div.graph--bar-group {
  flex-flow: row;
  justify-content: flex-start;
  align-items: flex-end;
  padding: 0;
  display: flex;
}

#graph--info--cont {
  color: var(--black);
  white-space: nowrap;
  background-color: var(--white);
  z-index: 2;
  border-radius: .2em;
  padding: .4em;
  font-size: 1.4em;
  line-height: 1.2em;
  display: none;
  position: absolute;
}

#workout-list svg.graph--bar-group {
  height: var(--graph-height);
  height: 8em;
}

.graph--heading {
  font-family: var(--ssb-font-family);
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  z-index: 1;
  font-size: 1em;
  font-weight: bold;
  position: relative;
  overflow: hidden;
}

.elevation--value {
  font-size: 1.6em;
  font-weight: bold;
  position: absolute;
  top: .4em;
}

.ascent--value {
  right: .3em;
}

.altitude--value {
  left: .3em;
}

.graph--msg {
  z-index: 1;
  font-size: 1em;
  line-height: 1.4em;
  position: relative;
}

.graph--cont {
  flex-flow: row;
  justify-content: flex-start;
  align-items: flex-end;
  width: 100%;
  height: 100%;
  padding: 0;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

.graph--bar {
  justify-content: center;
  align-items: flex-start;
  width: 1px;
  margin: 0;
  padding: 0;
  display: flex;
  position: relative;
}

.graph--info {
  color: var(--black);
  background-color: var(--white);
  z-index: 1;
  border-radius: 2px;
  padding: 2px;
  display: none;
  position: absolute;
  bottom: 60%;
}

.graph--bar:hover .graph--info {
  display: block;
}

.graph--info--power, .graph--info--slope, .graph--info--cadence {
  min-width: 48px;
}

.graph--info--time {
  color: var(--gray);
}

.workout--graph-cont {
  height: var(--graph-height);
  flex-flow: row;
  justify-content: center;
  align-items: flex-end;
  height: 100%;
  padding: 0;
  display: flex;
  position: relative;
}

.workout-list--graph-cont {
  flex-flow: row;
  justify-content: center;
  align-items: flex-end;
  padding: 0;
  display: flex;
  position: relative;
}

#progress {
  background: var(--progress-white);
  border-right: 1px solid var(--zone-orange);
  z-index: 1;
  pointer-events: none;
  justify-content: start;
  align-items: flex-end;
  width: 0;
  height: 100%;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

#progress-active {
  height: var(--graph-height);
  z-index: 1;
  pointer-events: none;
  justify-content: start;
  align-items: flex-end;
  width: 0;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

#targets {
  --inner-max-width: 300px;
  --mode-selector-height: 3rem;
  --mode-selector-margin: .8em auto;
  --mode-selector-padding: 0 1em;
  --mode-width: 100%;
  --number-input-height: 3rem;
  --number-input-total-width: 100%;
  --number-input-value-width: 40%;
  --number-input-width: 30%;
  --number-input-font-size: 1.2em;
  --number-input-value-font-size: 1.4em;
  padding: 0 1em;
  font-size: 1rem;
}

.targets--cont {
  max-width: var(--inner-max-width);
  margin: 0 auto;
}

.mode-selector {
  height: var(--mode-selector-height);
  margin: var(--mode-selector-margin);
  text-transform: uppercase;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  max-width: 100%;
  font-size: 1em;
  display: flex;
}

.mode {
  width: var(--mode-width);
  height: var(--mode-selector-height);
  line-height: var(--mode-selector-height);
  padding: var(--mode-selector-padding);
  color: var(--gray);
  cursor: pointer;
}

.mode.active {
  font-weight: bold;
}

.number-input {
  justify-content: center;
  align-items: center;
  display: flex;
}

.number-input--value, .number-input--inc, .number-input--dec {
  height: var(--number-input-height);
}

.number-input--value {
  width: var(--number-input-value-width);
  line-height: var(--number-input-line-height);
  font-size: var(--number-input-value-font-size);
  text-align: center;
  color: var(--dark);
  border: 1px solid var(--white-opacity-01);
}

.number-input--btn {
  font-size: var(--number-input-font-size);
  width: var(--number-input-width);
  text-align: center;
}

.number-input--inc {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.number-input--dec {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.fixed-bottom {
  border-top: 1px solid var(--white-opacity-01);
  --controls-height: 5em;
  --menu-height: 5em;
  --fb-total-height: 10em;
  --size--icon--global: 2.4em;
  --size--icon--menu--btn: 2em;
  height: var(--fb-total-height);
  background-color: var(--dark);
  z-index: 3;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: repeat(6, 1fr);
  align-items: center;
  width: 100%;
  font-size: 1rem;
  display: grid;
  position: fixed;
  bottom: 0;
}

#workout-name {
  font-size: 1.2em;
  font-family: var(--ssb-font-family);
  letter-spacing: 1px;
  text-transform: uppercase;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: left;
  grid-area: 1 / 1 / 2 / 4;
  margin: 0 .8em;
  font-weight: bold;
  display: block;
  overflow: hidden;
}

.watch {
  grid-area: 1 / 4 / 2 / 7;
  justify-self: right;
}

.watch .btn {
  margin: 0 2px;
}

.control--btn {
  margin: 0 1.2em;
  display: inline-block;
}

.control--btn--icon {
  width: var(--size--icon--global);
  height: var(--size--icon--global);
  fill: #fff;
  cursor: pointer;
}

#watch-pause, #watch-lap, #watch-stop, #activity-save {
  display: none;
}

.tab.active {
  display: block;
}

.tab {
  display: none;
}

.menu {
  height: var(--menu-height);
  grid-area: 2 / 1 / 2 / 7;
}

.menu--row {
  wrap: wrap;
  z-index: 1;
  flex-direction: row;
  place-content: stretch center;
  align-items: center;
  height: 100%;
  display: flex;
}

.menu--btn {
  cursor: pointer;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.menu--btn--icon {
